.commom-button {
  display: inline-flex;
  font-size: 0.875rem;
  font-family: "Gotu", sans-serif;
  font-weight: 400;
  padding: 1rem 5.5rem 1rem 2.5rem;
  border: 1px solid transparent;
  border-radius: 0;
  background: #408F95;
  color: #fff;
  position: relative;
  z-index: 1;
}

.commom-button::before {
  content: "";
  position: absolute;
  display: inline-block;
  border: 1px solid #408F95;
  top: 0.1125rem;
  left: 0.25rem;
  right: -0.35rem;
  bottom: -0.3875rem;
  z-index: -10;
}

.commom-button::after {
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -1;
}

.commom-button:hover {
  color: #408F95;
  background: #408F95;
}

.commom-button:hover::after {
  transform: scale(1, 1);
}

.arrow::after {
  background-image: url(https://raw.githubusercontent.com/nishiro1228/CodeUps-/0cfcef5a8b42bb890f780e2d40d729fcf6a0ee6f/src/images/common/Vector%204button-arrow.svg);
  background-repeat: no-repeat;
  background-size: contain;
  width: 20px;
}

.arrow::before {
  content: "";
  position: absolute;
  top: calc(50% - 2.2px);
  right: 2.7em;
  transform: translateY(calc(-50% + 1px)) rotate(45deg);
  width: 0.5375rem;
  height: 1px;
  background: #fff;
}

.arrow::after {
  content: "";
  position: absolute;
  top: 53%;
  right: 2.7em;
  transform: translateY(-50%);
  width: 2.3125rem;
  height: 1px;
  background-color: #fff;
}

.arrow:hover::before,
.arrow:hover:after {
  background: #408F95;
}

a:hover span::after,
a:hover span::before {
  background: #408F95;
}

