@use "global" as *;

.contact__container {
  display: flex;
  flex-direction: column;
  padding-left: rem(24);
  padding-right: rem(24);

  @include mq(md) {
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.25);
    flex-direction: initial;
    padding: rem(76.8) rem(50);
  }
}

.contact__left {
  text-align: center;
  @include mq(md) {
    border-right: 1px solid $green;
    padding-right: rem(86.8);
  }
}

.contact__logo {
  display: inline-block;
  max-width: rem(174);
  width:  100%;
  margin-bottom: rem(1.6);

  @include mq(md) {
    max-width: rem(200);
    margin-bottom: rem(4.8);
  }
}

.contact__logo img {
  aspect-ratio: 174/65;
  object-fit: cover;
  height: auto;

  @include mq(md) {
    aspect-ratio: 200/75;
  }
}

.contact__left-wrap {
  display: flex;
  flex-direction: column;
  padding-top: rem(16);
  border-top: 1px solid $green;

  @include mq(md) {
    flex-direction: initial;
    padding-top: rem(24);
  }
}

.contact__information {}

.contact__information-text {
  text-align: center;

  @include mq(md) {
    text-align: left;
  }
}
.contact__information-text + .contact__information-text {
  margin-top: rem(4);
}

.contact__googlemap {
  max-width: rem(295);
  width:  100%;
  margin-inline: auto;
  margin-top: rem(16);

  @include mq(md) {
    max-width: rem(273);
    margin-top: 0;
    margin-left: rem(5);
  }
}

.contact__googlemap iframe {
  aspect-ratio: 295/160;
  object-fit: cover;
  height: auto;
  width: 100%;

  @include mq(md) {
    aspect-ratio: 273/148;
  }
}

.contact__right {
  max-width: rem(315);
  width:  100%;
  margin-inline: auto;
  text-align: center;
  flex-grow: 1;
  margin-top: rem(33.6);

  @include mq(md) {
    margin-left: rem(81);
    margin-top: 0;
    padding-top: rem(25);
  }
}

.contact__right-text {
  font-size: rem(14);
  font-family: $base-font-family;
  font-weight: $regular;
  line-height: calc(18/14);
  color: $green;
  margin-top: rem(1.6);
}

.contact-button {}